.container {
  height: 87vh;
  margin: auto;
  text-align: center;
  padding: 20px;
  background-color: #fff;
}

.title {
  margin: 100px;
}

.header {
  font-size: 4em;
  color: #333;
  margin-bottom: 20px;
}

.top-description {
  font-size: 2em;
  color: #666;
  margin-bottom: 70px;
}

.bottom-description {
  font-size: 1.5em;
  line-height: 1.5;
  color: #666;
  margin: 75px;
  white-space: pre-line;
}

.ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
}

.card {
  padding: 50px;
  margin: 50px;
  border: solid #66666680 1px;
  border-radius: 10px;
  color: #333;
  display: inline-block;
}

.card-description {
  margin-bottom: 50px;
}

.button {
  padding: 15px 20px;
  margin: 10px;
  color: #fff;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button1 {
  background-color: #007bffab;
}

.button1:hover {
  background-color: #0056b3;
}

.button2 {
  background-color: #28a746bd;
}

.button2:hover {
  background-color: #1e7e34;
}

.button3 {
  background-color: #dc3546b2;
}

.button3:hover {
  background-color: #bd2130;
}
